import Layout from '../components/Layout';

const Auth = () => (
  <Layout>
    <section id="auth" className="container">
      <h1>Login</h1>
      <form>
        <div className="form-control">
          <label htmlFor="email">Email</label>
          <input type="email"/>
        </div>
        <div className="form-control">
          <label htmlFor="password">Password</label>
          <input type="password"/>
        </div>

      </form>





      <style jsx>{`
    
      .form-control {
        margin: 1rem 0;
      }

      label {
        display: block;
      }
      

    `}</style>
    
    </section>
  </Layout>
);

export default Auth;